const api = process.env.REACT_APP_CONTACTS_API_URL || 'http://localhost:5001'

let token = localStorage.token

if (!token)
  token = localStorage.token = Math.random().toString(36).substr(-8)

const headers = {
  'Accept': 'application/json',
  'Authorization': token
}

let contacts = [
    {
      id: 'richard',
      name: 'Richard Kalehoff',
      handle: '@richardkalehoff',
      avatarURL: './images/richard.jpg'
    },
    {
      id: 'karen',
      name: 'Karen Isgrigg',
      handle: '@karen_isgrigg',
      avatarURL: './images/karen.jpg'
    },
    {
      id: 'tyler',
      name: 'Tyler McGinnis',
      handle: '@tylermcginnis',
      avatarURL: './images/tyler.jpg'
    },
    { avatarURL:
      'data:image/jpeg;base64,',
     name: 'Sitansu Subudhi',
     handle: '@sksubudhi',
     id: 'sksubudhi' }
  ];


export const getAll = () =>
  fetch(`${api}/contacts`, { headers })
    .then(res => res.json())
    .then(data => data.contacts)

export function getAllContacts () {
  return new Promise((res, rej) => {
    setTimeout(() => res(contacts), 1000);
  })
}

export const remove = (contact) =>
  fetch(`${api}/contacts/${contact.id}`, { method: 'DELETE', headers })
    .then(res => res.json())
    .then(data => data.contact)

export const create = (contact) => {

  // fetch(`${api}/contacts`, {
  //   method: 'POST',
  //   headers: {
  //     ...headers,
  //     'Content-Type': 'application/json'
  //   },
  //   body: JSON.stringify(body)
  // }).then(res => res.json())

  if (!contact.id) {
    contact.id = Math.random().toString(36).substr(-8)
  }

  return new Promise((res, rej) => {
    setTimeout(() => res(contact), 1000);
  })
}
